<template>
    <yd-layout class="box_danger">
        <navBar title="隐患记录" slot="navbar"></navBar>
        <div v-if="!didLoadData">
            <div class="boxload">
                <div class="boxload_content">
                    <img src="../../assets/icon_loading.png" class="Rotation" >
                    <div>稍等哟~正在加载</div>
                </div>
            </div>
        </div>

        <div v-if="didLoadData && data.length == 0">
            <div class="boxload">
                <div class="boxload_content">
                    <img src="../../assets/icon_lose.png" >
                    <div>哎呀!还没有数据</div>
                </div>
            </div>
        </div>
        <div v-if="didLoadData && data.length > 0">
            <yd-pullrefresh :callback="doRefreshData" ref="listViewRefresh">
                <yd-infinitescroll :callback="doLoadMoreData" ref="listViewLoadMore">
                    <yd-list slot="list">
                        <!--隐患记录-->
                        <div class="div_list" v-for="item,index in data" :key="index">
                            <!--检查点-->
                            <div class="list_top">
                                <img src="../../assets/shu.png" alt="" class="shu">
                                <p class="list_title">{{item.xjdmc}}</p>
                                <div style="flex: 1;"></div>
                                <p class="list_btn_red" v-if="item.yhzt == 1 && item.zgright == 1" @click="toCorrect(0, item)">整 改</p>
                                <p class="list_btn_orange" v-if="item.yhzt == 2 && item.fcright == 1" @click="toCorrect(1, item)">复 查</p>
                            </div>
                            <!--时间-->
                            <div>{{item.jcsj}}</div>

                            <div class="list_cont">
                                <div class="list_cont_result posi">
                                    <div v-if="item.yhzt == 1" style="color: red">待整改</div>
                                    <div v-if="item.yhzt == 2" style="color: orange;">待复查</div>
                                    <div v-if="item.yhzt == 3" style="color: gray;">复查结束</div>
                                    <p>结果</p>
                                </div>
                                <div class="list_cont_result">
                                    <div>{{item.bcmc}}</div>
                                    <p>班次</p>
                                </div>
                                <div class="list_cont_result" style="margin-right: 0">
                                    <div>{{item.jcry == undefined ?  '无' : item.jcry}}</div>
                                    <p>检查人</p>
                                </div>
                            </div>
                            <div class="list_org">
                                <p class="org">公司</p>
                                <p style="flex: 1;">{{item.qymc}}</p>
                            </div>
                            <div class="list_org">
                                <p class="org">备注</p>
                                <p style="flex: 1;">{{item.bz}}</p>
                            </div>
                            <!--隐患图片-->
                            <div class="list_org">
                                <p class="org">附件</p>
                                <div class="flex_image">
                                    <div class="flex_image_img">
                                        <img :src="item.yhnr" alt="" @click="onPressLightbox($event,item.yhnr)">
                                    </div>
                                </div>
                            </div>
                            <!--整改记录-->
                            <div v-if="item.recordnum">
                                <yd-accordion >
                                    <yd-accordion-item title="整改记录">
                                        <div class="list_record" v-for="items,key in item.history" :key="key">
                                            <div>
                                                <div style="color: #1D91E6;">整改</div>
                                                <div>整改金额：{{items.yhzgje}}元</div>
                                                <div>整改人员：{{item.yhzgr}}</div>
                                                <div>整改时间：{{items.yhzgsj}}</div>
                                                <div>整改备注：{{items.yhzgbz}}</div>
                                                <div class="box_img" @click="onPressLightbox($event,items.yhzgzp)">
                                                    <img :src="items.yhzgzp" alt="">
                                                </div>
                                            </div>
                                            <div v-if="items.yhclzt">
                                                <div style="color: orange">复查</div>
                                                <div>复查金额：{{items.yhfcje}}元</div>
                                                <div>复查人员：{{item.yhfcr}}</div>
                                                <div>复查时间：{{items.yhfcsj}}</div>
                                                <div>复查备注：{{items.yhfcbz}}</div>
                                                <div class="box_img" @click="onPressLightbox($event,items.yhfczp)">
                                                    <img :src="items.yhfczp" alt="">
                                                </div>
                                            </div>
                                        </div>
                                    </yd-accordion-item>
                                </yd-accordion>
                            </div>

                        </div>
                    </yd-list>
                </yd-infinitescroll>
            </yd-pullrefresh>
        </div>
    </yd-layout>
</template>

<script>
    import {api_xunjian_danger} from '../../components/APIKeys'
    export default {
        name: "DangerList",
        data(){
            return{
                // token:window.localStorage.getItem('token'),
                data:[],//获取的数据
                recordnum:null,//是否显示整改记录
                hasMore: true,//是否有下一页
                page: 1,//页数
                pageSize: 10,//每页个数
                didLoadData:false, //是否加载完数据
            }
        },
        mounted(){
            this.doLoadDanger((res) => {
                this.data = [...res.data.data];
            });
        },
        methods:{
            //获取数据
            doLoadDanger(data){
                // this.$showDialog();
                let jsonData = {
                    // token: this.token,
                    pageIndex: this.page,
                    pageSize: this.pageSize,
              }
              this.$post(api_xunjian_danger, jsonData).then(res =>{
                  // this.$showDialog(false);
                  this.didLoadData = true;
                  if(res.errno == 0){
                      data && data(res);
                      // this.data = res.data.data || [];
                      this.hasMore = res.data.hasMore;
                      for (let i = 0; i < this.data.length; i++) {
                          if (this.data[i].history.length > 0) {
                              this.data[i].recordnum = true;
                          } else {
                              this.data[i].recordnum = false;
                          }
                      }
                  }else{
                      this.$showError('获取信息失败');
                  }
              })

            },
            /**
             * 上拉加载
             */
            doLoadMoreData: function () {
                // console.log('this.hasMore========= ',this.hasMore);
                if (!this.hasMore) {
                    this.$refs.listViewLoadMore.$emit('ydui.infinitescroll.loadedDone');
                    return;
                }
                this.page++;
                let self = this;
                this.doLoadDanger((res) => {
                    self.data = [...self.data, ...res.data.data];
                    // self.hasMore = self.data.length < parseInt(res.total);
                    self.hasMore = res.data.hasMore;

                    if (self.data.length > 0) {
                        // 所有数据加载完成
                        if (!self.hasMore) {
                            self.$refs.listViewLoadMore.$emit('ydui.infinitescroll.loadedDone');
                        }
                        else {
                            /* 单次请求数据完毕 */
                            self.$refs.listViewLoadMore.$emit('ydui.infinitescroll.finishLoad');
                        }
                    }
                })
            },
            /**
             * 下拉刷新
             */
            doRefreshData: function () {
                this.page = 1;
                let self = this;
                this.doLoadDanger((res) => {
                    self.data = [...res.data.data];
                    if (self.$refs.listViewRefresh) {
                        self.$refs.listViewRefresh.$emit('ydui.pullrefresh.finishLoad');
                    }
                });
            },
            //点击放大图片
            onPressLightbox(e, img) {
                let images = [{url: img},];
                this.$showLightbox(images);
                // fancyBox(e.target, images);
            },
            //点击页面跳转
            toCorrect(index,item){
                window.localStorage.setItem('danger',JSON.stringify(item));
                //去整改
                if(index == 0){
                    this.$pushRoute('DangerCorrect');
                }else{
                    this.$pushRoute('DangerRecheck');
                }
            },
        }
    }
</script>

<style scoped>
    .boxload {
        height: 3.8rem;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .boxload_content{
        width: 3rem;
        height:2.7rem;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
    }
    .boxload_content img{
        width: 1.54rem;
        height: 1.54rem;
    }
    .boxload_content div{
        color: #cdcdcd;
        margin-top: 0.2rem;
    }
    @-webkit-keyframes rotation{
        from {-webkit-transform: rotate(0deg);}
        to {-webkit-transform: rotate(360deg);}
    }

    .Rotation{
        transform: rotate(360deg);
        animation: rotation 1s linear infinite;
    }
    .div_list{
        margin: .24rem;
        background: #fff;
        padding: .24rem;

    }
    .list_top{
        display: flex;
        align-items: center;
        margin-bottom: 0.2rem;
    }
    .shu{
        width: 0.05rem;
        height: 0.3rem;
        margin-right: .2rem;
    }
    .list_title{
        font-size: .34rem;
        font-weight: 600;
    }
    .list_cont{
        display: flex;
        align-items: center;
        margin-top:0.2rem;
    }
    .list_cont_result{
        flex: 1;
        margin-right: 0.1rem;
        text-align: center;
        background: #f8f8f8;
        border-radius: 5px;
        padding: .2rem;
    }
    .list_cont_result p{
        font-size: .26rem;
        color: #909090;
    }
    .posi{
        position:relative;
    }
    .list_org{
        margin-top: .2rem;
        display: flex;
    }
    .list_org .org{
        color: #909090;
        margin-right: .2rem;
    }
    .list_btn_red{
        padding: 0 .24rem;
        border: 1px solid red;
        color: red;
    }
    .list_btn_orange{
        padding: 0 .24rem;
        border: 1px solid orange;
        color: orange;
    }
    .list_record{
        padding: .24rem;
        color: #101010;
    }
    .record_imgbox{
        flex: 1;
        display: flex;
        align-items: center;
    }
    .box_img {
        width: calc(95% / 3);
        height: 1.8rem;
        margin-right: .1rem;
        margin-bottom: .1rem;
    }

    .box_img img {
        width: 100%;
        height: 100%;
    }
    .flex_image{
        flex: 1;
        display: flex;
        align-items: center;
    }
    .flex_image_img{
        width: calc(95% / 3);
        height: 1.8rem;
    }
    .flex_image_img img{
        width: 100%;
        height: 100%;
    }
</style>
<style>
    .box_danger .yd-accordion-title-full{
        background: #EEEEEE;
        padding: 0 .24rem;
    }
    .box_danger .yd-accordion-head{
        padding: 0;
    }
    .box_danger .yd-accordion-head-arrow{
        background: #EEEEEE;
        padding-right: .24rem;
    }
    .box_danger .yd-accordion-title{
        color: #000;
    }
    .box_danger .yd-accordion{
        margin-top: .2rem;
    }
</style>



